// gift

.gift {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  min-height: 289px;
  padding-top: 138px;
  padding-right: 61px;
  padding-bottom: 43px;
  padding-left: 58px;
  background-color: @page-green;
  background-image: url("../img/gift-photo-m.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &::before {
    content: "";
    position: absolute;
    top: 58px;
    left: 50%;
    width: 78px;
    height: 80px;
    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transform: translateX(-50%);
  }

  @media (min-width: @tablet-width) {
    min-height: 200px;
    align-items: flex-start;
    justify-content: flex-end;
    padding-top: 74px;
    padding-right: 124px;
    background-image: url("../img/gift-photo-t.jpg");

    &::before {
      left: 78px;
      width: 80px;
      height: 82px;
      transform: translateX(0);
    }
  }

  @media (min-width: @desktop-ultra-w) {
    justify-content: flex-start;
    align-items: flex-end;
    min-height: 289px;
    padding-top: 138px;
    padding-right: 43px;
    padding-bottom: 42px;
    padding-left: 41px;
    background-image: url("../img/gift-photo-d.jpg");

    &::before {
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

.gift__text {
  color: @basic-white;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.25;
  font-weight: 400;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 161px;
    text-align: left;
  }

  @media (min-width: @desktop-ultra-w) {
    line-height: 1.25;
    text-align: center;
  }
}
