// contacts

.contacts {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.contacts__title {
  width: 42%;
  font-size: 16px;
  line-height: 1.35;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    width: 30%;
    font-size: 20px;
    line-height: 1.25;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 163px;
  }
}

.contacts__address {
  width: 50%;
  font-size: 14px;
  line-height: 1.42;
  font-weight: 400;
  font-family: "Arial", sans-serif;

  @media (min-width: @tablet-width) {
    width: 25%;
    font-size: 16px;
    line-height: 1.25;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 155px;
  }
}

.contacts__text {
  @media (min-width: @tablet-width) {
    text-align: right;
  }

  @media (min-width: @desktop-ultra-w) {
    text-align: left;
  }
}

.contacts__text-separator {
  @media (min-width: @tablet-width) {
    display: block;
    padding-top: 20px;
  }
}
