// footer

.footer {
  @media (min-width: @desktop-ultra-w) {
    background-color: @page-grey;
  }
}

.footer--inner {
  background-color: @page-grey;
}

.footer__content {
  padding-top: 28px;
  padding-bottom: 25px;

  @media (min-width: @tablet-width) {
    padding-top: 64px;
    padding-right: 72px;
    padding-bottom: 57px;
    padding-left: 72px;
  }

  @media (min-width: @desktop-ultra-w) {
    position: relative;
    width: @desktop-ultra-w;
    .centered();
    padding: 0;
  }
}

.footer__contacts {
  @media (min-width: @desktop-ultra-w) {
    position: absolute;
    top: 98px;
    left: 110px;
    z-index: 101;
    width: 570px;
    min-height: 201px;
    padding-top: 56px;
    padding-right: 90px;
    padding-bottom: 51px;
    padding-left: 76px;
    background-color: @basic-white;
  }
}

.footer__content--bg {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-bottom: 22px;
  background-color: @page-grey;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 53px;
    padding-right: 30px;
    padding-left: 30px;
  }

  @media (min-width: @desktop-ultra-w) {
    padding-top: 57px;
    padding-right: 110px;
    padding-left: 110px;
    padding-bottom: 58px;
    background-color: transparent;
  }
}

.footer__logo {
  padding-top: 10px;
  padding-bottom: 32px;

  @media (min-width: @tablet-width) {
    padding: 0;
    padding-right: 22px;
  }
}

.footer__socials {
  justify-content: space-around;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 17px;
  border-top: 1px solid @page-grey-m;
  border-bottom: 1px solid @page-grey-m;

  @media (min-width: @tablet-width) {
    justify-content: space-between;
    width: 130px;
    padding: 0;
    border: none;
  }
}

.footer__copy {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-top: 19px;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.125;

  @media (min-width: @tablet-width) {
    width: 163px;
    padding: 0;
  }
}

// * states

.footer__socials ._socials__icon,
.footer__copy ._copyright__icon-copy {
  &:hover {
    fill: @page-green;
  }

  &:active {
    opacity: 0.3;
  }
}
