// feature

.feature {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  padding-right: 20px;
  padding-bottom: 21px;
  padding-left: 20px;
  background-color: @page-grey;
  word-break: normal;

  &::before {
    content: "";
    position: absolute;
    top: 22px;
    left: 22px;
    width: 50px;
    height: 53px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100%;
  }

  @media (min-width: @tablet-width) {
    align-items: flex-start;
    padding-top: 73px;
    padding-right: 52px;
    padding-bottom: 55px;
    padding-left: 52px;

    &::before {
      top: 50%;
      left: auto;
      right: 63px;
      width: 200px;
      height: 211px;
      transform: translateY(-50%);
    }
  }

  @media (min-width: @desktop-ultra-w) {
    align-items: center;
    padding-top: 78px;

    &::before {
      top: 42px;
      left: 52px;
      right: auto;
      width: 100px;
      height: 106px;
      transform: translateY(0);
    }
  }
}

.feature--slim {
  &::before {
    background-image: url("../img/cat-slim.svg");
  }
}

.feature--gain {
  &::before {
    top: 24px;
    left: 12px;
    width: 67px;
    height: 50px;
    background-image: url("../img/cat-gain.svg");
  }

  .feature__title {
    padding-left: 40px;

    @media (min-width: @tablet-width) {
      padding-left: 0;
    }

    @media (min-width: @desktop-ultra-w) {
      padding-left: 62px;
    }
  }

  @media (min-width: @tablet-width) {
    &::before {
      right: 30px;
      width: 268px;
      height: 200px;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    &::before {
      top: 48px;
      left: 33px;
      width: 134px;
      height: 100px;
    }
  }
}

.feature__title {
  margin-bottom: 31px;
  padding-left: 11px;
  font-size: 24px;
  line-height: 1.54;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    margin-bottom: 25px;
    padding-left: 0;
    font-size: 36px;
    line-height: 1;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 74px;
    padding-left: 24px;
  }
}

.feature__text {
  margin-bottom: 43px;
  line-height: 1.33;

  @media (min-width: @tablet-width) {
    width: 62%;
    min-height: 96px;
    margin-bottom: 52px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 100%;
    min-height: auto;
    margin-bottom: 26px;
  }
}

.feature__link {
  position: relative;
  align-self: flex-start;
  padding-right: 46px;
  color: currentColor;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    top: 3px;
    right: 0;
    width: 32px;
    height: 10px;
    background-image: url("../img/feature-arrow.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: -5px 0;
    transition-property: background-position;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 1.5;

    &::before {
      top: 10px;
      right: -12px;
    }
  }

  &:hover::before {
    background-position: 0 0;
  }

  &:active {
    opacity: 0.3;
  }
}
