// page-subtitle

.page-subtitle {
  position: relative;
  display: inline-block;
  padding-right: 20px;
  font-size: 24px;
  line-height: 1;
  font-weight: 400;
  text-transform: none;
  background-color: @basic-white;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: calc(50% + 3px);
    left: 0;
    width: 100vw;
    height: 2px;
    background-color: @page-green;
    z-index: -1;
    transform: translateY(-50%);
  }

  &::before {
    display: none;
  }

  @media (min-width: @tablet-width) {
    padding-right: 35px;
    padding-left: 37px;
    font-size: 36px;

    br {
      display: none;
    }

    &::before {
      display: block;
      left: auto;
      right: 100%;
      height: 3px;
    }

    &::after {
      height: 3px;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    padding-right: 36px;
    padding-left: 0;

    &::before,
    &::after {
      top: calc(50% + 6px);
    }

    &::before {
      margin-right: 38px;
    }
  }
}

.page-subtitle--inner {
  line-height: 1.083;

  @media (min-width: @tablet-width) {
    &::before {
      margin-right: 30px;
    }
  }
}
