// instruct

.instruct {
  position: relative;
  padding-left: 80px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.28;
  word-break: normal;

  &::before {
    content: "";
    position: absolute;
    top: -4px;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: @page-green;
    background-image: url("../img/how-leaf.png");
    background-repeat: no-repeat;
    background-size: 30px 32px;
    background-position: center;
  }

  &:nth-child(2n + 1) {
    padding-right: 20px;
  }

  @media (min-width: @tablet-width) {
    padding-left: 0;

    &::before {
      top: 40px;
      width: 80px;
      height: 80px;
    }

    &::after {
      content: "1";
      position: absolute;
      z-index: -1;
      top: 0;
      right: 95px;
      font-family: @font-name;
      color: @page-grey;
      font-size: 280px;
      line-height: 1;
    }

    &:nth-child(2n + 1) {
      padding-right: 0;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    &::after {
      right: 0;
    }
  }
}

.instruct--tub {
  &::before {
    background-image: url("../img/how-tub.png");
    background-size: 29px 31px;
  }

  @media (min-width: @tablet-width) {
    &::after {
      content: "2";
      right: 80px;
    }
  }
}

.instruct--eat {
  &::before {
    background-image: url("../img/how-eat.png");
    background-size: 25px 30px;
  }

  @media (min-width: @tablet-width) {
    &::after {
      content: "3";
      right: 78px;
    }
  }
}

.instruct--alarm {
  &::before {
    background-image: url("../img/how-alarm.png");
    background-size: 32px 33px;
  }

  @media (min-width: @tablet-width) {
    &::after {
      content: "4";
      right: 77px;
    }
  }
}

.instruct__text {
  @media (min-width: @tablet-width) {
    width: 70%;
    margin-top: 152px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: auto;
  }
}
