// annotation-text

.annotation-text {
  color: @basic-white;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet-width) {
    color: @basic-black;
    font-size: 20px;
    text-align: left;
  }
}

.annotation-text--inner {
  color: @basic-black;
  line-height: 1.28;
  text-align: left;

  @media (min-width: @tablet-width) {
    line-height: 1.5;
  }
}
