// page

.page__body {
  position: relative;
}

.page__body--index {
  @media (min-width: @desktop-ultra-w) {
    &::before {
      content: "";
      position: absolute;
      z-index: -1;
      top: 0;
      right: 0;
      width: 50%;
      height: 696px;
      background-color: @page-green;
      background-image: url("../img/index-background-d.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }
  }
}

.page__body--index .header__logo {
  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 1;
  }
}

.page__body--index .header__site-link {
  @media (min-width: @desktop-ultra-w) {
    color: @basic-white;
  }
}

.page__body--index .header__site-link--current {
  @media (min-width: @desktop-ultra-w) {
    &::before {
      bottom: 2px;
      background-color: @basic-white;
    }
  }
}

.page__main {
  overflow: hidden;
}
