// promo

.promo {
  position: relative;
  color: @basic-white;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100%;
    height: 305px;
    background-color: @page-green;
    background-image: url("../img/index-background-m.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  @media (min-width: @tablet-width) {
    &::before {
      display: none;
    }
  }
}

.promo__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-top: 27px;

  @media (min-width: @tablet-width) {
    align-items: flex-start;
    padding-top: 42px;
    padding-left: 117px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 111px;
    padding-left: 190px;
    padding-bottom: 100px;
  }
}

.promo__title {
  width: 90%;
  min-height: 72px;
  margin-bottom: 25px;
  color: @basic-white;

  @media (min-width: @tablet-width) {
    width: 67%;
    margin-bottom: 41px;
    color: @basic-black;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 414px;
  }
}

.promo__slogan {
  margin-bottom: 28px;

  @media (min-width: @tablet-width) {
    margin-bottom: 49px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 51px;
  }
}

.promo__image {
  width: 280px;
  height: 270px;
  margin-bottom: 5px;

  img {
    width: 100%;
  }

  @media (min-width: @tablet-width) {
    z-index: -1;
    order: 1;
    width: 709px;
    height: 609px;
    margin-top: -82px;
    margin-left: -88px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 552px;
    height: 449px;
    margin-top: -367px;
    margin-left: calc(50% - 202px);
  }
}

.promo__button {
  @media (min-width: @tablet-width) {
    width: auto;
    margin-left: -5px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-left: 0;
  }
}
