// example

.example {
  background-color: @page-grey-l;

  @media (min-width: @tablet-width) {
    background-color: transparent;
    background-image: linear-gradient(0deg, @page-grey-l 42%, @basic-white 42%);
    border-top: 1px solid @page-grey-l;
  }

  @media (min-width: @desktop-ultra-w) {
    background-image: linear-gradient(0deg, @page-grey-l 70%, @basic-white 70%);
  }
}

.example__content {
  padding-top: 27px;
  padding-bottom: 25px;

  @media (min-width: @tablet-width) {
    padding-top: 40px;
    padding-bottom: 64px;
  }

  @media (min-width: @desktop-ultra-w) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: @desktop-ultra-w;
    .centered();
    padding-top: 0;
    padding-right: 70px;
    padding-left: 110px;
  }
}

.example__overall {
  margin-bottom: 42px;
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    padding-right: 30px;
    padding-left: 30px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 0;
    padding: 0;
    padding-top: 57px;
  }
}

.example__title {
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    margin-bottom: 65px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 148px;
  }
}

.example__text {
  margin-bottom: 25px;
  line-height: 1.3;

  @media (min-width: @tablet-width) {
    margin-bottom: 53px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 431px;
    margin-bottom: 68px;
  }
}

.example__grid {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 87px;
  }

  @media (min-width: @desktop-ultra-w) {
    flex-direction: column;
    align-items: flex-start;
    width: 366px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 0;
  }
}

.example__stats {
  margin-bottom: 26px;

  @media (min-width: @tablet-width) {
    width: 51.6%;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 100%;
    margin-bottom: 62px;
  }
}

.example__res {
  font-size: 14px;
  line-height: 1.42;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: flex-end;
    font-size: 20px;
    line-height: 1;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 327px;
    flex-direction: row;
    justify-content: space-between;
  }
}
