// _site-list

._site-list {
  display: none;
  flex-direction: column;
  align-items: center;
  flex-basis: 100%;
  .mp-reset();
  list-style: none;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    flex-basis: auto;
  }
}

._site-list__link {
  display: block;
  text-decoration: none;
  align-items: center;
  color: currentColor;
}
