//  catalog

.catalog__content {
  padding-top: 27px;
  padding-right: 0;
  padding-left: 0;

  @media (min-width: @tablet-width) {
    padding-top: 43px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 23px;
  }
}

.catalog__title {
  margin-bottom: 40px;
  padding-right: 20px;
  padding-left: 20px;
  text-align: left;

  @media (min-width: @tablet-width) {
    margin-bottom: 70px;
    padding-right: 0;
    padding-left: 0;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 66px;
  }
}

.catalog__list {
  .mp-reset();
  list-style: none;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }
}

.catalog__card {
  @media (min-width: @tablet-width) {
    width: 45.7%;
    margin-right: 60px;
    margin-bottom: 47px;

    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    width: 245px;
    margin-right: 80px;
    margin-bottom: 42px;

    &:nth-child(2n) {
      margin-right: 80px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }
}
