// additional

.additional__content {
  padding-top: 27px;
  padding-bottom: 34px;

  @media (min-width: @tablet-width) {
    padding-top: 17px;
    padding-bottom: 77px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 32px;
    padding-bottom: 86px;
  }
}

.additional__title {
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    margin-bottom: 78px;
  }
}

.additional__grid {
  @media (min-width: @desktop-ultra-w) {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.additional__list {
  .mp-reset();
  list-style: none;
  margin-bottom: 57px;

  @media (min-width: @tablet-width) {
    margin-bottom: 68px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 896px;
    margin-bottom: 0;
  }
}

.additional__item {
  margin-bottom: 18px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}

.additional__gift {
  @media (min-width: @desktop-ultra-w) {
    width: 245px;
  }
}
