// compare

.compare {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}

.compare__images {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 240px;
  margin-bottom: 14px;

  @media (min-width: @tablet-width) {
    margin-bottom: 10px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 33px;
  }
}

.compare__image {
  position: absolute;
  width: auto;
  overflow: hidden;
  background-color: @page-grey-l;

  @media (min-width: @tablet-width) {
    background-color: transparent;
  }
}

.compare__image img {
  max-width: none;

  @media (min-width: @tablet-width) {
    width: 690px;
  }
}

.compare__image--dynamic {
  top: 0;
  left: calc(50% - 311px / 2);
  width: 311px;

  @media (min-width: @tablet-width) {
    top: auto;
    left: calc(50% - 690px / 2);
    bottom: 0;
    width: 100%;
  }
}

.compare__image--static {
  position: static;
  top: auto;
  left: auto;
}

.compare__controls {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 228px;

  @media (min-width: @tablet-width) {
    width: 590px;
  }
}

.compare__button {
  .mp-reset();
  width: 70px;
  min-height: 40px;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  border: none;
  border-bottom: 2px dotted @basic-black;
  background-color: transparent;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 1.482;
  }

  &:hover {
    border-bottom-style: solid;
  }
}

// ! slider

.compare__slider {
  position: relative;
  width: 84px;
  height: 14px;
  border: 1px solid @page-grey-m;
  border-radius: 12px;
  background-color: @basic-white;

  @media (min-width: @tablet-width) {
    width: 428px;
    height: 6px;
    border: none;
    background-color: @page-grey-m;
  }
}

.compare__pin {
  position: absolute;
  top: 50%;
  left: 5px;
  width: 35px;
  height: 6px;
  border-radius: 12px;
  background-color: @page-green;
  transform: translateY(-50%);
  filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    background-color: @basic-white;
    border: 1px solid @page-grey-m;
    border-radius: 100%;
    cursor: w-resize;

    &::before {
      content: "";
      width: 8px;
      height: 8px;
      background-color: @page-green;
      border-radius: 100%;
    }
  }

  &:hover {
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
  }

  &:active {
    border: 1px solid #68b738;
    box-sizing: border-box;
  }
}

// * states

.compare--init {
  .compare__image--dynamic {
    width: 0;
  }

  .compare__image--static {
    opacity: 1;
  }

  .compare__pin {
    left: calc(100% - (35px + 5px));
  }

  @media (min-width: @tablet-width) {
    .compare__image--dynamic {
      width: 0;
      opacity: 1;
    }

    .compare__pin {
      left: calc(100% - (34px + 5px));
    }
  }
}
