// stats

.stats {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.stats__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 44.2%;
  min-height: 64px;
  padding-top: 12px;
  border: 1px solid @basic-neutral;
  border-radius: 3px;

  @media (min-width: @tablet-width) {
    min-height: 70px;
    padding-top: 14px;
  }
}

.stats__definition {
  font-size: 24px;
  line-height: 1;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 1.233;
  }
}

.stats__term {
  order: 1;
  width: 80px;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  background-color: @page-grey-l;
  transform: translateY(10px);

  @media (min-width: @tablet-width) {
    width: 140px;
    font-size: 14px;
    background-color: @basic-white;
  }

  @media (min-width: @desktop-ultra-w) {
    background-color: @page-grey-l;
  }
}
