// form

.form__content {
  padding-top: 29px;
  padding-bottom: 32px;

  @media (min-width: @tablet-width) {
    padding-top: 43px;
    padding-bottom: 71px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 22px;
    padding-bottom: 79px;
  }
}

.form__title {
  margin-bottom: 38px;
  text-align: left;

  @media (min-width: @tablet-width) {
    margin-bottom: 55px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 56px;
  }
}

.form__slogan {
  width: 85%;
  margin-bottom: 39px;

  @media (min-width: @tablet-width) {
    width: 50%;
    margin-left: 40px;
    margin-bottom: 65px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 323px;
    margin-bottom: 66px;
    margin-left: 0;
  }
}

.form__list {
  @media (min-width: @tablet-width) {
    padding-right: 37px;
    padding-left: 40px;
  }

  @media (min-width: @desktop-ultra-w) {
    padding: 0;
  }
}
