// card

.card {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;
  padding-right: 18px;
  padding-bottom: 24px;

  border: 1px solid @page-grey-m;
  border-bottom: none;

  @media (min-width: @tablet-width) {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-right: 36px;
    padding-left: 36px;
    padding-bottom: 40px;
    border: none;
    background-image: linear-gradient(0deg, @page-grey 82%, @basic-white 82%);
  }
}

.card__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 138px;
  min-height: 112px;

  @media (min-width: @tablet-width) {
    align-items: flex-end;
    width: 168px;
    height: 200px;
    margin-bottom: 34px;
  }

  @media (min-width: @desktop-ultra-w) {
    margin-bottom: 43px;
  }
}

.card__img {
  width: 100%;
}

.card__grid {
  width: 50.5%;
  margin-bottom: 18px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    margin-bottom: 35px;
  }
}

.card__title {
  width: 70%;
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 1.25;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    width: 55%;
    margin-bottom: 18px;
    font-size: 20px;
    line-height: 1.2;
    text-align: center;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 145px;
    margin-bottom: 19px;
    line-height: 1.05;
  }
}

.card__spec {
  width: 100%;
  font-size: 12px;
  line-height: 1.333;
  font-weight: 400;
  font-family: "Arial", sans-serif;
  word-break: break-all;
  border-collapse: collapse;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 1;

    tr {
      border-bottom: 1px solid @page-grey-m;
    }
  }
}

.card__key,
.card__value {
  @media (min-width: @tablet-width) {
    padding-top: 5px;
    padding-bottom: 8px;
  }

  @media (min-width: @desktop-ultra-w) {
    padding-top: 6px;
  }
}

.card__value {
  text-align: right;
}

.card__button {
  font-size: 16px;
  line-height: 1.25;
  color: @basic-white;

  @media (min-width: @tablet-width) {
    padding-top: 9px;
    padding-bottom: 9px;
    font-size: 16px;
  }
}

// more-button

.card--more {
  flex-direction: column;
  align-items: center;
  padding-top: 27px;
  border-bottom: 1px solid @page-grey-m;

  @media (min-width: @tablet-width) {
    position: relative;
    padding-top: 145px;
    background-image: none;
    border: 1px solid @page-grey-m;

    &::before {
      content: "";
      position: absolute;
      top: -1px;
      left: -1px;
      width: 100.6%;
      height: 98px;
      background-color: @basic-white;
      border-bottom: 1px solid @page-grey-m;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    padding-top: 150px;

    &::before {
      left: -1px;
      width: 246px;
    }
  }

  .card__link {
    position: relative;
    width: 30px;
    min-height: 30px;
    margin-bottom: 22px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: @page-grey-m;
      transform: translateY(-50%);
    }

    &::after {
      top: 48%;
      transform: rotate(90deg);
    }

    @media (min-width: @tablet-width) {
      align-items: center;
      width: 60px;
      min-height: 60px;
      height: auto;
      margin-bottom: 48px;
    }

    @media (min-width: @desktop-ultra-w) {
      margin-bottom: 52px;
    }
  }

  .card__grid {
    width: 100%;
    margin-bottom: 22px;

    @media (min-width: @tablet-width) {
      margin-bottom: 86px;
    }
  }

  .card__title {
    width: auto;
    text-align: center;
    margin-bottom: 9px;

    @media (min-width: @tablet-width) {
      width: 60%;
      margin-bottom: 22px;
    }

    @media (min-width: @desktop-ultra-w) {
      width: 134px;
      margin-bottom: 24px;
    }
  }

  .card__text {
    font-size: 12px;
    line-height: 1.333;
    font-family: "Arial", sans-serif;
    text-align: center;

    @media (min-width: @tablet-width) {
      width: 70%;
      font-size: 16px;
      line-height: 1.125;
    }

    @media (min-width: @desktop-ultra-w) {
      width: 167px;
    }
  }

  .card__button {
    color: @basic-black;
    background-color: @page-grey;

    &:hover {
      background-color: @page-grey-l;
    }
  }
}
