@font-face {
  font-family: "Oswald";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("oswaldregular"), url("../fonts/oswaldregular.woff2") format("woff2"), local("oswald regular"), url("../fonts/oswaldregular.woff") format("woff");
}

@font-face {
  font-family: "Oswald";
  font-display: swap;
  font-style: normal;
  font-weight: 500;
  src: local("oswaldmedium"), url("../fonts/oswaldmedium.woff2") format("woff2"), local("oswald medium"), url("../fonts/oswaldmedium.woff") format("woff");
}

/* Указываем box sizing */

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внешние отступы */

body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

/* Выставляем основные настройки по-умолчанию для body */

body {
  min-width: 320px;
  min-height: 100vh;
  overflow-y: scroll;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

/* Упрощаем работу с изображениями */

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

._content {
  padding-right: 20px;
  padding-left: 20px;
}

._head {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

._navigation {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

._navigation--nojs ._burger {
  display: none;
}

._navigation--nojs ._site-list {
  display: flex;
}

._burger {
  background-image: url("../img/icon-burger.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}

._navigation--active ._burger {
  background-image: url("../img/icon-cross.svg");
}

._navigation--active ._burger__icon-menu {
  display: none;
}

._navigation--active ._burger__icon-cross {
  display: block;
}

._navigation--active ._site-list {
  display: flex;
}

._burger__icon-cross {
  display: none;
}

._logo {
  display: block;
}

._burger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  margin: 0;
  padding: 0;
  margin-left: auto;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

._burger__icon {
  width: 100%;
}

._site-list {
  display: none;
  flex-direction: column;
  align-items: center;
  flex-basis: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

._site-list__link {
  display: block;
  text-decoration: none;
  align-items: center;
  color: currentColor;
}

._socials {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

._socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  color: currentColor;
  background-image: url("../img/icon-insta.svg");
  background-repeat: no-repeat;
  background-position: center;
}

._socials__link:hover {
  opacity: 0.6;
}

._socials__link:active {
  opacity: 0.3;
}

._socials__link--insta {
  background-image: url("../img/icon-insta.svg");
}

._socials__link--fb {
  background-image: url("../img/icon-fb.svg");
}

._socials__link--tw {
  background-image: url("../img/icon-twitter.svg");
}

._socials__icon {
  fill: currentColor;
}

._address {
  font-style: normal;
}

._address__phone,
._address__mail {
  color: currentColor;
  text-decoration: none;
}

._map {
  background-color: #d7dcde;
  min-height: 362px;
  height: 200px;
}

._copyright__icon {
  display: block;
  background-image: url("../img/icon-footer-html.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.common-field {
  margin: 0;
  padding: 0;
  border: none;
}

.common-field__content {
  margin: 0;
  padding: 0;
  list-style: none;
}

.common-field__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 13px;
}

.common-field__item:last-child {
  margin-bottom: 0;
}

.common-field__item--spec {
  display: block;
}

.common-field__label {
  margin-right: 16px;
}

.common-field__label--spec {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 0;
}

.common-field__input {
  flex-grow: 1;
  padding-top: 13px;
  padding-right: 14px;
  padding-bottom: 11px;
  padding-left: 14px;
  text-transform: inherit;
  border: 1px solid #ebebeb;
}

.common-field__input:hover {
  border-color: #cdcdcd;
}

.common-field__input:active {
  border-color: #222222;
}

.common-field__input:focus {
  border-color: #222222;
}

.common-field__input:focus:invalid {
  border-color: #ff8282;
}

.common-field__input:focus:valid {
  border-color: #68b738;
}

.common-field__input:focus:valid + .common-field__icon {
  fill: #68b738;
  stroke: #68b738;
}

.common-field__input:focus:invalid + .common-field__icon {
  fill: #ff8282;
  stroke: #ff8282;
}

.common-field__input--spec {
  flex-grow: 1;
  margin-left: 10px;
  padding-right: 34px;
}

.common-field__icon {
  position: absolute;
  right: 14px;
  top: 50%;
  fill: #222222;
  transform: translateY(-50%);
}

.common-field__icon--mail {
  stroke: #222222;
}

.common-field__textarea {
  width: 100%;
  min-height: 150px;
  padding-top: 13px;
  padding-right: 12px;
  padding-bottom: 13px;
  padding-left: 14px;
  text-transform: inherit;
  border: 1px solid #ebebeb;
  resize: none;
}

.custom-elem__content {
  margin: 0;
  padding: 0;
  list-style: none;
}

.custom-elem__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 30px;
  margin-bottom: 13px;
}

.custom-elem__item:last-child {
  margin-bottom: 0;
}

.custom-elem__label {
  position: relative;
  padding-left: 45px;
  user-select: none;
  cursor: pointer;
}

.custom-elem__label::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  border: 1px solid #ebebeb;
}

.custom-elem__label::after {
  content: "";
  position: absolute;
  display: none;
  top: 0;
  left: 0;
  width: 13.5px;
  height: 13.5px;
  background-color: #68b738;
}

.custom-elem__label:hover::before {
  border-color: #cdcdcd;
}

.custom-elem__label:active::before,
.custom-elem__label:active::after {
  opacity: 0.3;
}

.custom-elem__label--radio::before {
  top: -5px;
  border-radius: 100%;
}

.custom-elem__label--radio::after {
  top: 50%;
  left: 8px;
  border-radius: 100%;
  transform: translateY(-50%);
}

.custom-elem__label--checkbox::before {
  top: -5px;
}

.custom-elem__label--checkbox::after {
  top: 50%;
  left: 6px;
  width: 18px;
  height: 10px;
  background-color: transparent;
  border: 2px solid #68b738;
  border-top: none;
  border-right: none;
  transform: translateY(-75%) rotate(-45deg);
}

.custom-elem__input:checked ~ .custom-elem__label::after {
  display: block;
}

.custom-elem__input:disabled ~ .custom-elem__label::before,
.custom-elem__input:disabled ~ .custom-elem__label::after {
  opacity: 0.6;
}

.header__content {
  padding-right: 0;
  padding-left: 0;
}

.header__head {
  width: 100%;
  padding-top: 12px;
  padding-right: 20px;
  padding-bottom: 16px;
  padding-left: 20px;
  border-bottom: 1px solid #ebebeb;
}

.header__logo {
  width: 190px;
  height: 37px;
}

.header__logo img {
  width: 100%;
}

.header__logo:hover {
  opacity: 0.6;
}

.header__logo:active {
  opacity: 0.3;
}

.header__site-list li {
  width: 100%;
}

.header__site-link {
  padding-top: 22px;
  padding-bottom: 23px;
  font-size: 20px;
  line-height: 1;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 1px solid #ebebeb;
}

.header__site-link:hover {
  opacity: 0.6;
}

.header__site-link:active {
  opacity: 0.3;
}

.header__site-link--current {
  position: relative;
}

.header__site-link--current::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #68b738;
}

.page__body {
  position: relative;
}

.page__body--index .header__logo:hover {
  opacity: 1;
}

.page__body--index .header__logo:active {
  opacity: 1;
}

.page__main {
  overflow: hidden;
}

.page-title {
  color: #000000;
  font-size: 36px;
  line-height: 1;
  font-weight: 400;
  text-align: center;
}

.page-title--sub {
  color: #000000;
  line-height: 1.11;
  text-align: left;
}

.page-text {
  color: #000000;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.28;
  font-weight: 400;
}

.annotation-text {
  color: #ffffff;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;
}

.annotation-text--inner {
  color: #000000;
  line-height: 1.28;
  text-align: left;
}

.page-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 168px;
  min-height: 40px;
  margin: 0;
  padding: 0;
  padding: 7px 44px;
  color: currentColor;
  font-size: 14px;
  line-height: 1.28;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  background-color: #68b738;
}

.page-button:hover {
  background-color: #5eaa2f;
}

.page-button:active {
  opacity: 0.3;
}

.footer--inner {
  background-color: #f2f2f2;
}

.footer__content {
  padding-top: 28px;
  padding-bottom: 25px;
}

.footer__content--bg {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-bottom: 22px;
  background-color: #f2f2f2;
}

.footer__logo {
  padding-top: 10px;
  padding-bottom: 32px;
}

.footer__socials {
  justify-content: space-around;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 17px;
  border-top: 1px solid #cdcdcd;
  border-bottom: 1px solid #cdcdcd;
}

.footer__copy {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-top: 19px;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.125;
}

.footer__socials ._socials__icon:hover,
.footer__copy ._copyright__icon-copy:hover {
  fill: #68b738;
}

.footer__socials ._socials__icon:active,
.footer__copy ._copyright__icon-copy:active {
  opacity: 0.3;
}

.contacts {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.contacts__title {
  width: 42%;
  font-size: 16px;
  line-height: 1.35;
  font-weight: 400;
  text-transform: uppercase;
}

.contacts__address {
  width: 50%;
  font-size: 14px;
  line-height: 1.42;
  font-weight: 400;
  font-family: "Arial", sans-serif;
}

.page-subtitle {
  position: relative;
  display: inline-block;
  padding-right: 20px;
  font-size: 24px;
  line-height: 1;
  font-weight: 400;
  text-transform: none;
  background-color: #ffffff;
}

.page-subtitle::before,
.page-subtitle::after {
  content: "";
  position: absolute;
  top: calc(50% + 3px);
  left: 0;
  width: 100vw;
  height: 2px;
  background-color: #68b738;
  z-index: -1;
  transform: translateY(-50%);
}

.page-subtitle::before {
  display: none;
}

.page-subtitle--inner {
  line-height: 1.083;
}

.promo {
  position: relative;
  color: #ffffff;
}

.promo::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 305px;
  background-color: #68b738;
  background-image: url("../img/index-background-m.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.promo__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-top: 27px;
}

.promo__title {
  width: 90%;
  min-height: 72px;
  margin-bottom: 25px;
  color: #ffffff;
}

.promo__slogan {
  margin-bottom: 28px;
}

.promo__image {
  width: 280px;
  height: 270px;
  margin-bottom: 5px;
}

.promo__image img {
  width: 100%;
}

.features__content {
  padding-top: 20px;
  padding-bottom: 20px;
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #000000;
}

.features__list li {
  margin-bottom: 20px;
}

.features__list li:last-child {
  margin-bottom: 0;
}

.feature {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
  padding-right: 20px;
  padding-bottom: 21px;
  padding-left: 20px;
  background-color: #f2f2f2;
  word-break: normal;
}

.feature::before {
  content: "";
  position: absolute;
  top: 22px;
  left: 22px;
  width: 50px;
  height: 53px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.feature--slim::before {
  background-image: url("../img/cat-slim.svg");
}

.feature--gain::before {
  top: 24px;
  left: 12px;
  width: 67px;
  height: 50px;
  background-image: url("../img/cat-gain.svg");
}

.feature--gain .feature__title {
  padding-left: 40px;
}

.feature__title {
  margin-bottom: 31px;
  padding-left: 11px;
  font-size: 24px;
  line-height: 1.54;
  font-weight: 400;
  text-transform: uppercase;
}

.feature__text {
  margin-bottom: 43px;
  line-height: 1.33;
}

.feature__link {
  position: relative;
  align-self: flex-start;
  padding-right: 46px;
  color: currentColor;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
}

.feature__link::before {
  content: "";
  position: absolute;
  top: 3px;
  right: 0;
  width: 32px;
  height: 10px;
  background-image: url("../img/feature-arrow.svg");
  background-repeat: no-repeat;
  background-size: contain;
