// cat-form

.cat-form {
  font-size: 16px;
  line-height: 1.28;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 1.482;
  }

  @media (min-width: @desktop-ultra-w) {
    display: flex;
    flex-wrap: wrap;
  }
}

.cat-form__subtitle {
  margin-bottom: 32px;

  @media (min-width: @tablet-width) {
    margin-bottom: 75px;
    padding-left: 0;
  }
}

.cat-form__subtitle--additional {
  @media (min-width: @tablet-width) {
    margin-bottom: 57px;
  }
}

.cat-form__fullname {
  margin-bottom: 24px;

  @media (min-width: @tablet-width) {
    margin-bottom: 72px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 569px;
    margin-bottom: 0;
  }
}

.cat-form__course {
  position: relative;
  .mp-reset();
  margin-bottom: 26px;
  padding-top: 20px;
  padding-bottom: 22px;
  border: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: -20px;
    width: 100vw;
    height: 1px;
    background-color: @page-grey-m;
  }

  &::after {
    top: auto;
    bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 75px;
    padding-top: 25px;
    padding-right: 28px;
    padding-left: 28px;
    border: 1px solid @page-grey-m;

    &::before,
    &::after {
      display: none;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    width: 569px;
    margin-bottom: 69px;
    margin-left: auto;
  }
}

.cat-form__onwer {
  margin-bottom: 28px;

  @media (min-width: @tablet-width) {
    margin-bottom: 64px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 100%;
    margin-bottom: 66px;

    .common-field__content {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
    }

    .common-field__item {
      width: 571px;
      margin-bottom: 0;
    }
  }
}

.cat-form__commentary {
  margin-bottom: 27px;

  @media (min-width: @tablet-width) {
    margin-bottom: 82px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 100%;
    margin-bottom: 60px;

    .common-field__textarea {
      min-height: 174px;
    }
  }
}

.cat-form__additional {
  .mp-reset();
  margin-bottom: 33px;
  border: none;

  @media (min-width: @tablet-width) {
    margin-bottom: 25px;

    .custom-elem__content {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    .custom-elem__item {
      width: 45.5%;
      margin-bottom: 30px;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    width: 100%;
    margin-bottom: 60px;

    .custom-elem__item {
      width: 245px;
      margin-bottom: 0;
    }
  }
}

.cat-form__submit-field {
  .mp-reset();
  border: none;

  @media (min-width: @desktop-ultra-w) {
    width: 100%;
  }
}

.cat-form__submit-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (min-width: @tablet-width) {
    align-items: center;
    justify-content: flex-start;
  }
}

.cat-form__submit-button {
  margin-bottom: 22px;
  color: @basic-white;
  font-size: 16px;
  line-height: 1.25;

  @media (min-width: @tablet-width) {
    width: 45%;
    margin-right: 60px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 568px;
    margin-right: 83px;
  }
}

.cat-form__submit-required {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.14;
  font-weight: 400;
  text-transform: none;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 1;
  }
}
