// _burger

._burger {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  .mp-reset();
  margin-left: auto;
  border: none;
  background-color: transparent;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

._burger__icon {
  width: 100%;
}
