// features

.features__content {
  padding-top: 20px;
  padding-bottom: 20px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 80px;
  }
}

.features__list {
  .mp-reset();
  list-style: none;
  color: @basic-black;

  li {
    margin-bottom: 20px;
  }

  li:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    li {
      margin-bottom: 34px;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    li {
      width: 570px;
      margin-bottom: 0;
    }
  }
}
