// header

.header__content {
  padding-right: 0;
  padding-left: 0;

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
  }
}

.header__navigation {
  @media (min-width: @tablet-width) {
    padding-top: 23px;
    padding-bottom: 23px;
  }

  @media (min-width: @desktop-ultra-w) {
    padding-top: 53px;
    padding-bottom: 53px;
  }
}

.header__navigation--index {
  @media (min-width: @desktop-ultra-w) {
    padding-bottom: 0;
  }
}

.header__head {
  width: 100%;
  padding-top: 12px;
  padding-right: @pr-m;
  padding-bottom: 16px;
  padding-left: @pl-m;
  border-bottom: 1px solid @page-grey-l;

  @media (min-width: @tablet-width) {
    width: auto;
    padding: 0;
    border-bottom: none;
  }
}

.header__logo {
  width: 190px;
  height: 37px;

  img {
    width: 100%;
  }

  @media (min-width: @tablet-width) {
    width: 174px;
    height: 52px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 202px;
    height: 59px;
  }

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.header__site-list {
  li {
    width: 100%;
  }

  @media (min-width: @tablet-width) {
    flex-wrap: wrap;
    align-items: center;
    width: 441px;
    margin-left: auto;

    li {
      width: auto;
      margin-right: 35px;
    }

    li:last-child {
      margin-right: 0;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    width: 482px;
  }
}

.header__site-link {
  padding-top: 22px;
  padding-bottom: 23px;
  font-size: 20px;
  line-height: 1;
  font-weight: 500;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 1px solid @page-grey-l;

  @media (min-width: @tablet-width) {
    padding-top: 20px;
    padding-bottom: 9px;
    font-size: 18px;
    line-height: 1.333;
    font-weight: 400;
    border-bottom: none;
  }

  @media (min-width: @desktop-ultra-w) {
    padding-top: 16px;
    font-size: 20px;
    line-height: 1.48;
  }

  // * states
  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.header__site-link--current {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: @page-green;
  }

  @media (min-width: @desktop-ultra-w) {
    &::before {
      bottom: 2px;
    }
  }
}
