//  _socials

._socials {
  display: flex;
  flex-wrap: wrap;
  .mp-reset();
  list-style: none;
}

._socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  color: currentColor;
  background-image: url("../img/icon-insta.svg");
  background-repeat: no-repeat;
  background-position: center;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

// * ie fallback

._socials__link--insta {
  background-image: url("../img/icon-insta.svg");
}

._socials__link--fb {
  background-image: url("../img/icon-fb.svg");
}

._socials__link--tw {
  background-image: url("../img/icon-twitter.svg");
}

._socials__icon {
  fill: currentColor;
}
