// how-works

.how-works__content {
  padding-top: 6px;
  padding-bottom: 47px;

  @media (min-width: @tablet-width) {
    padding-top: 26px;
    padding-bottom: 12px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: @desktop-ultra-w;
    .centered();
    padding-top: 55px;
    padding-bottom: 11px;
  }
}

.how-works__title {
  margin-bottom: 43px;

  @media (min-width: @tablet-width) {
    margin-bottom: 32px;
  }
}

.how-works__list {
  .mp-reset();
  list-style: none;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.how-works__item {
  min-height: 51px;
  margin-bottom: 29px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    width: 45.8%;
    min-height: 280px;
    margin-bottom: 37px;

    &:last-child {
      margin-bottom: 37px;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    width: 246px;
    margin-bottom: 0;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
