// good-item

.good-item {
  word-break: break-all;
  border-top: 1px solid @page-grey-m;

  @media (min-width: @tablet-width) {
    padding-top: 26px;
    padding-right: 37px;
    padding-bottom: 27px;
    padding-left: 38px;
    border: 1px solid @page-grey-m;
    border-bottom: none;

    &:last-child {
      border-bottom: 1px solid @page-grey-m;
    }
  }

  @media (min-width: @desktop-ultra-w) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 17px;
    padding-bottom: 14px;
    padding-left: 1px;
    padding-right: 0;
    border-right: none;
    border-left: none;
  }
}

.good-item__table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 14px;

  @media (min-width: @tablet-width) {
    margin-bottom: 23px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 650px;
    margin-bottom: 0;
  }
}

.good-item__body {
  @media (min-width: @tablet-width) {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.good-item__title {
  padding-top: 13px;
  padding-left: 2px;
  padding-bottom: 13px;
  font-size: 16px;
  line-height: 1;
  font-weight: 400;
  text-transform: uppercase;
  text-align: left;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    padding: 0;
  }
}

.good-item__spec {
  padding-bottom: 14px;
  font-size: 14px;
  line-height: 1;
  font-family: "Arial", sans-serif;
  font-weight: 400;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 1.25;
    padding-top: 4px;
    padding-bottom: 0;
  }

  @media (min-width: @desktop-ultra-w) {
    padding-top: 9px;
  }
}

.good-item__key,
.good-item__value {
  padding: 0;

  @media (min-width: @desktop-ultra-w) {
    text-align: left;
  }
}

.good-item__key {
  padding-right: 10px;

  @media (min-width: @tablet-width) {
    width: 144px;
    text-align: right;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 245px;
  }
}

.good-item__value {
  text-align: right;

  @media (min-width: @tablet-width) {
    width: 84px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 80px;
    padding-right: 10px;
  }
}

.good-item__button {
  color: @basic-white;
  font-size: 16px;
  line-height: 1;

  @media (min-width: @tablet-width) {
    min-height: 38px;
    padding-top: 7px;
    padding-bottom: 6px;
    font-size: 16px;
  }

  @media (min-width: @desktop-ultra-w) {
    width: 244px;
    min-height: 40px;
  }
}
